<template>
  <div>
    <el-tree
      :data="menus"
      show-checkbox
      node-key="name"
      ref="tree"
      :default-expanded-keys="['学生管理']"  
      :default-checked-keys="['学生列表']"
      :props="defaultProps">
    </el-tree>
    <el-button @click="getCheckedNodes">通过 node 获取</el-button>
  <el-button @click="getCheckedKeys">通过 key 获取</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menus: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },

  created() {
    this.menus = [...this.$router.options.routes]
    this.menus = this.menus.filter(obj => !obj.hidden)
  },

  methods: {
    getCheckedNodes() {
      console.log(this.$refs.tree.getCheckedNodes());
    },
    getCheckedKeys() {
      console.log(this.$refs.tree.getCheckedKeys());
    },
  },
}
</script>
